'use client';

import React, { useState, useEffect, useCallback } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import Link from 'next/link';
import {
  Search,
  MapPin,
  Briefcase,
  GraduationCap,
  Filter,
  X,
  Loader2,
  Users,
  ChevronDown,
  Navigation,
} from 'lucide-react';
import { searchUsersForMention, getNearbyUsers } from '@/lib/api/posts';
import type { MentionUser } from '@/types/post';

interface User {
  id: string;
  username: string;
  name: string;
  profileImage: string | null;
  headline?: string | null;
  college?: string | null;
  location?: string | null;
  distance?: number;
  isNearby?: boolean;
}

interface FilterOptions {
  sortBy: 'name' | 'distance' | 'recent';
  radius: number;
  showNearbyOnly: boolean;
}

function UserCard({ user }: { user: User }) {
  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      className="bg-white dark:bg-neutral-900 rounded-xl border border-gray-200 dark:border-neutral-800 p-4 hover:border-gray-300 dark:hover:border-neutral-700 transition-all hover:shadow-md"
    >
      <Link href={`/profile/${user.username}`} className="flex items-start gap-4">
        {/* Avatar */}
        <div className="w-16 h-16 rounded-full overflow-hidden bg-gray-200 dark:bg-neutral-700 flex-shrink-0">
          {user.profileImage ? (
            <img
              src={user.profileImage}
              alt={user.name}
              className="w-full h-full object-cover"
            />
          ) : (
            <div className="w-full h-full flex items-center justify-center text-2xl font-bold text-gray-500">
              {user.name.charAt(0).toUpperCase()}
            </div>
          )}
        </div>
        
        {/* Info */}
        <div className="flex-1 min-w-0">
          <h3 className="font-semibold text-gray-900 dark:text-white truncate">
            {user.name}
          </h3>
          <p className="text-sm text-gray-500 dark:text-neutral-400">
            @{user.username}
          </p>
          
          {user.headline && (
            <p className="text-sm text-gray-600 dark:text-neutral-300 mt-1 line-clamp-2">
              {user.headline}
            </p>
          )}
          
          <div className="flex flex-wrap items-center gap-3 mt-2 text-xs text-gray-400 dark:text-neutral-500">
            {user.college && (
              <span className="flex items-center gap-1">
                <GraduationCap className="w-3 h-3" />
                {user.college}
              </span>
            )}
            {user.location && (
              <span className="flex items-center gap-1">
                <MapPin className="w-3 h-3" />
                {user.location}
              </span>
            )}
            {user.isNearby && user.distance !== undefined && (
              <span className="flex items-center gap-1 text-green-600 dark:text-green-400">
                <Navigation className="w-3 h-3" />
                {user.distance < 1 ? `${Math.round(user.distance * 1000)}m` : `${user.distance.toFixed(1)}km`} away
              </span>
            )}
          </div>
        </div>
      </Link>
    </motion.div>
  );
}

export function FindPeople() {
  const [searchQuery, setSearchQuery] = useState('');
  const [users, setUsers] = useState<User[]>([]);
  const [nearbyUsers, setNearbyUsers] = useState<User[]>([]);
  const [loading, setLoading] = useState(false);
  const [loadingNearby, setLoadingNearby] = useState(true);
  const [showFilters, setShowFilters] = useState(false);
  const [locationEnabled, setLocationEnabled] = useState(false);
  const [locationError, setLocationError] = useState<string | null>(null);
  
  const [filters, setFilters] = useState<FilterOptions>({
    sortBy: 'name',
    radius: 50,
    showNearbyOnly: false,
  });

  // Fetch nearby users on mount
  useEffect(() => {
    const fetchNearbyUsers = async () => {
      setLoadingNearby(true);
      try {
        const nearby = await getNearbyUsers(filters.radius, 20);
        const formattedNearby = nearby.map((u: any) => ({
          ...u,
          isNearby: true,
        }));
        setNearbyUsers(formattedNearby);
        setLocationEnabled(true);
      } catch (error: any) {
        console.error('Error fetching nearby users:', error);
        if (error.response?.status === 403) {
          setLocationError('Location access is required to find nearby users');
        }
      } finally {
        setLoadingNearby(false);
      }
    };
    
    fetchNearbyUsers();
  }, [filters.radius]);

  // Search users
  const handleSearch = useCallback(async (query: string) => {
    if (query.length < 2) {
      setUsers([]);
      return;
    }
    
    setLoading(true);
    try {
      const results = await searchUsersForMention(query);
      setUsers(results.map((u: MentionUser) => ({
        id: u.id,
        username: u.username,
        name: u.name,
        profileImage: u.profileImage,
      })));
    } catch (error) {
      console.error('Error searching users:', error);
    } finally {
      setLoading(false);
    }
  }, []);

  // Debounced search
  useEffect(() => {
    const timer = setTimeout(() => {
      handleSearch(searchQuery);
    }, 300);
    
    return () => clearTimeout(timer);
  }, [searchQuery, handleSearch]);

  // Request location permission
  const requestLocation = async () => {
    setLocationError(null);
    
    try {
      const position = await new Promise<GeolocationPosition>((resolve, reject) => {
        navigator.geolocation.getCurrentPosition(resolve, reject, {
          enableHighAccuracy: true,
          timeout: 10000,
        });
      });
      
      // Update location on backend
      const { updateLocation } = await import('@/lib/api/posts');
      await updateLocation(position.coords.latitude, position.coords.longitude, position.coords.accuracy);
      
      // Refetch nearby users
      setLoadingNearby(true);
      const nearby = await getNearbyUsers(filters.radius, 20);
      setNearbyUsers(nearby.map((u: any) => ({ ...u, isNearby: true })));
      setLocationEnabled(true);
    } catch (error: any) {
      console.error('Location error:', error);
      if (error.code === 1) {
        setLocationError('Location permission denied. Please enable location access in your browser settings.');
      } else {
        setLocationError('Failed to get your location. Please try again.');
      }
    } finally {
      setLoadingNearby(false);
    }
  };

  // Get displayed users
  const displayedUsers = searchQuery.length >= 2 ? users : (filters.showNearbyOnly ? nearbyUsers : nearbyUsers);

  // Sort users
  const sortedUsers = [...displayedUsers].sort((a, b) => {
    switch (filters.sortBy) {
      case 'distance':
        return (a.distance || Infinity) - (b.distance || Infinity);
      case 'name':
      default:
        return a.name.localeCompare(b.name);
    }
  });

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-neutral-950 pb-24">
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className="sticky top-0 z-40 bg-white/80 dark:bg-neutral-900/80 backdrop-blur-sm border-b border-gray-200 dark:border-neutral-800">
          <div className="p-4">
            <h1 className="text-2xl font-bold text-gray-900 dark:text-white mb-4">
              Find People
            </h1>
            
            {/* Search Bar */}
            <div className="relative">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
              <input
                type="text"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                placeholder="Search by name or username..."
                className="w-full pl-10 pr-4 py-3 rounded-xl bg-gray-100 dark:bg-neutral-800 text-gray-900 dark:text-white placeholder-gray-500 outline-none focus:ring-2 focus:ring-blue-500 transition-all"
              />
              {searchQuery && (
                <button
                  onClick={() => setSearchQuery('')}
                  className="absolute right-3 top-1/2 -translate-y-1/2 p-1 rounded-full hover:bg-gray-200 dark:hover:bg-neutral-700"
                >
                  <X className="w-4 h-4 text-gray-400" />
                </button>
              )}
            </div>
            
            {/* Filters Toggle */}
            <button
              onClick={() => setShowFilters(!showFilters)}
              className="flex items-center gap-2 mt-3 text-sm text-gray-600 dark:text-neutral-400 hover:text-gray-800 dark:hover:text-neutral-200 transition-colors"
            >
              <Filter className="w-4 h-4" />
              Filters
              <ChevronDown className={`w-4 h-4 transition-transform ${showFilters ? 'rotate-180' : ''}`} />
            </button>
          </div>
          
          {/* Filters Panel */}
          <AnimatePresence>
            {showFilters && (
              <motion.div
                initial={{ height: 0, opacity: 0 }}
                animate={{ height: 'auto', opacity: 1 }}
                exit={{ height: 0, opacity: 0 }}
                className="overflow-hidden border-t border-gray-100 dark:border-neutral-800"
              >
                <div className="p-4 space-y-4">
                  {/* Sort By */}
                  <div>
                    <label className="text-sm font-medium text-gray-700 dark:text-neutral-300 mb-2 block">
                      Sort by
                    </label>
                    <div className="flex gap-2">
                      {[
                        { value: 'name', label: 'Name' },
                        { value: 'distance', label: 'Distance' },
                        { value: 'recent', label: 'Recently Active' },
                      ].map((option) => (
                        <button
                          key={option.value}
                          onClick={() => setFilters({ ...filters, sortBy: option.value as any })}
                          className={`px-4 py-2 rounded-full text-sm font-medium transition-colors ${
                            filters.sortBy === option.value
                              ? 'bg-blue-500 text-white'
                              : 'bg-gray-100 dark:bg-neutral-800 text-gray-700 dark:text-neutral-300 hover:bg-gray-200 dark:hover:bg-neutral-700'
                          }`}
                        >
                          {option.label}
                        </button>
                      ))}
                    </div>
                  </div>
                  
                  {/* Radius */}
                  <div>
                    <label className="text-sm font-medium text-gray-700 dark:text-neutral-300 mb-2 block">
                      Nearby radius: {filters.radius} km
                    </label>
                    <input
                      type="range"
                      min={5}
                      max={200}
                      value={filters.radius}
                      onChange={(e) => setFilters({ ...filters, radius: Number(e.target.value) })}
                      className="w-full"
                    />
                  </div>
                  
                  {/* Show Nearby Only */}
                  <label className="flex items-center gap-2 cursor-pointer">
                    <input
                      type="checkbox"
                      checked={filters.showNearbyOnly}
                      onChange={(e) => setFilters({ ...filters, showNearbyOnly: e.target.checked })}
                      className="w-4 h-4 rounded"
                    />
                    <span className="text-sm text-gray-700 dark:text-neutral-300">
                      Show nearby users only
                    </span>
                  </label>
                </div>
              </motion.div>
            )}
          </AnimatePresence>
        </div>

        {/* Content */}
        <div className="p-4">
          {/* Location Permission Card */}
          {!locationEnabled && !loadingNearby && (
            <div className="mb-6 p-6 bg-gradient-to-r from-blue-50 to-purple-50 dark:from-blue-900/20 dark:to-purple-900/20 rounded-xl border border-blue-200 dark:border-blue-800/50">
              <div className="flex items-start gap-4">
                <div className="p-3 rounded-full bg-blue-100 dark:bg-blue-900/50">
                  <MapPin className="w-6 h-6 text-blue-600 dark:text-blue-400" />
                </div>
                <div className="flex-1">
                  <h3 className="font-semibold text-gray-900 dark:text-white">
                    Discover people nearby
                  </h3>
                  <p className="text-sm text-gray-600 dark:text-neutral-400 mt-1">
                    Enable location to find students and professionals in your area.
                  </p>
                  {locationError && (
                    <p className="text-sm text-red-500 mt-2">{locationError}</p>
                  )}
                  <button
                    onClick={requestLocation}
                    className="mt-3 px-4 py-2 bg-blue-500 text-white rounded-lg text-sm font-medium hover:bg-blue-600 transition-colors"
                  >
                    Enable Location
                  </button>
                </div>
              </div>
            </div>
          )}

          {/* Loading State */}
          {(loading || loadingNearby) && (
            <div className="flex items-center justify-center py-12">
              <Loader2 className="w-8 h-8 animate-spin text-gray-400" />
            </div>
          )}

          {/* Search Results */}
          {searchQuery.length >= 2 && !loading && (
            <div className="mb-4">
              <h2 className="text-sm font-medium text-gray-500 dark:text-neutral-400 mb-3">
                Search results for "{searchQuery}"
              </h2>
              {users.length === 0 ? (
                <div className="text-center py-8">
                  <Users className="w-12 h-12 mx-auto text-gray-300 dark:text-neutral-600 mb-2" />
                  <p className="text-gray-500 dark:text-neutral-400">No users found</p>
                </div>
              ) : (
                <div className="grid gap-4 sm:grid-cols-2">
                  {users.map((user) => (
                    <UserCard key={user.id} user={user} />
                  ))}
                </div>
              )}
            </div>
          )}

          {/* Nearby Users */}
          {searchQuery.length < 2 && !loadingNearby && (
            <>
              {nearbyUsers.length > 0 && (
                <div>
                  <h2 className="text-sm font-medium text-gray-500 dark:text-neutral-400 mb-3 flex items-center gap-2">
                    <MapPin className="w-4 h-4" />
                    People nearby
                  </h2>
                  <div className="grid gap-4 sm:grid-cols-2">
                    {sortedUsers.map((user) => (
                      <UserCard key={user.id} user={user} />
                    ))}
                  </div>
                </div>
              )}

              {nearbyUsers.length === 0 && locationEnabled && (
                <div className="text-center py-12">
                  <MapPin className="w-12 h-12 mx-auto text-gray-300 dark:text-neutral-600 mb-2" />
                  <p className="text-gray-500 dark:text-neutral-400">No one nearby</p>
                  <p className="text-sm text-gray-400 dark:text-neutral-500">
                    Try increasing the search radius
                  </p>
                </div>
              )}
            </>
          )}
        </div>
      </div>
    </div>
  );
}

export default FindPeople;
